@import "../../global.scss";
.portfolio{
    background-color:wheat;
    display:flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
        color: goldenrod;
    }
    ul{
        margin:10px;
        padding:0;
        display: flex;
        list-style: none;
        background-color: lightgray;

        li{
            font-size: 14px;
            margin-right:50px;
            padding:7px;
            border-radius: 10px;
            cursor: pointer;
            &.active{
                background-color: $mainColor;
                color:white;
            }
        }
    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        .item{
            width: 220px;
            height: 220px;
            border-radius: 20px;
            border: 1px solid red;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;
            h3{
                position:absolute;
                font-size:20px;
                padding: 5px 5px;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 2;
              }
        
              &:hover {
                background-color: gray;
                img {
                  opacity: 0.2;
                  z-index: 0;

                }
              }
        }
    }
}